import React from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import useIntersectionObserver from '../hooks/useIntersectionObserver';

const Section = styled(motion.section)`
  background-color: #000;
  padding: 100px;
  text-align: center;
  
  @media (max-width: 768px) {
    padding: 50px 20px;
  }
`;

const SectionTitle = styled(motion.h2)`
  font-size: 36px;
  margin-bottom: 40px;
  color: #03fffb;
`;

const Grid = styled(motion.div)`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 30px;
  justify-items: center;
  margin-bottom: 50px;
`;

const Card = styled(motion.div)`
  max-width: 300px;
  width: 100%;
  border: 1px solid #333;
  border-radius: 8px;
  overflow: hidden;
  background-color: #0a0a0a;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  flex-direction: column;

  &:hover {
    transform: translateY(-8px);
    box-shadow: 0 8px 16px rgba(3, 255, 251, 0.2);
  }
`;

// Placeholder for images since none were provided
const ImagePlaceholder = styled.div`
  width: 100%;
  height: 200px;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #333;
  font-size: 2rem;
  font-weight: bold;
`;

const Details = styled.div`
  padding: 20px;
  flex: 1;
  display: flex;
  flex-direction: column;
`;

const Title = styled.h3`
  font-size: 24px;
  margin-bottom: 10px;
  color: #07d20d;
`;

const Description = styled.p`
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 20px;
  color: #ccc;
  flex: 1;
`;

const LinkButton = styled.a`
  display: inline-block;
  color: #03fffb;
  text-decoration: none;
  padding: 10px 20px;
  border: 2px solid #03fffb;
  border-radius: 4px;
  transition: all 0.3s ease;
  font-weight: 600;

  &:hover,
  &:focus {
    background-color: #03fffb;
    color: #000;
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(3, 255, 251, 0.3);
  }

  &:focus {
    outline: 2px solid #03fffb;
    outline-offset: 2px;
  }
`;

const Badge = styled.span`
  display: inline-block;
  padding: 6px 12px;
  background-color: #333;
  color: #888;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 1px;
`;

const apps = [
    {
        title: 'Repurpose AI',
        description: 'AI-powered content repurposing SaaS that transforms long-form content into platform-specific posts.',
        status: 'Live',
        // Using a placeholder link as none was provided, user can update
        link: '#',
        linkText: 'Check it out'
    },
    {
        title: 'Repurpose AI Chrome Extension',
        description: 'Browser extension to streamline your content repurposing workflow directly from your browser.',
        status: 'Coming Soon',
        link: null,
        linkText: null
    },
];

function VibeCodedApps() {
    const [sectionRef, isSectionVisible] = useIntersectionObserver({ threshold: 0.1, triggerOnce: true });

    const containerVariants = {
        hidden: { opacity: 0, y: 50 },
        visible: {
            opacity: 1,
            y: 0,
            transition: {
                duration: 0.6,
                staggerChildren: 0.2,
            },
        },
    };

    const itemVariants = {
        hidden: { opacity: 0, y: 50, scale: 0.9 },
        visible: {
            opacity: 1,
            y: 0,
            scale: 1,
            transition: { duration: 0.5 },
        },
    };

    return (
        <Section
            id="vibe-coded-apps"
            ref={sectionRef}
            variants={containerVariants}
            initial="hidden"
            animate={isSectionVisible ? "visible" : "hidden"}
        >
            <div className="container">
                <SectionTitle variants={itemVariants}>Vibe-Coded Apps</SectionTitle>
                <Grid variants={containerVariants}>
                    {apps.map((app) => (
                        <Card key={app.title} variants={itemVariants}>
                            <ImagePlaceholder>
                                {/* Visual placeholder - could be an icon or text */}
                                AI
                            </ImagePlaceholder>
                            <Details>
                                <Title>{app.title}</Title>
                                <Description>
                                    {app.description}
                                </Description>
                                <div style={{ marginTop: 'auto' }}>
                                    {app.status === 'Coming Soon' ? (
                                        <Badge>COMING SOON</Badge>
                                    ) : (
                                        <LinkButton
                                            href={app.link}
                                            target="_blank"
                                            rel="noopener noreferrer"
                                            aria-label={`View ${app.title}`}
                                        >
                                            {app.linkText}
                                        </LinkButton>
                                    )}
                                </div>
                            </Details>
                        </Card>
                    ))}
                </Grid>
            </div>
        </Section>
    );
}

export default VibeCodedApps;
